.book {
  position: absolute;
  top: 50%;
  left: 30%;
  width: 40%;
  height: 8vh;
  background-color: black;
  border: 2px dotted white;
  border-radius: 20px;
  color: white;
  font-size: 1.5rem;
  font-family: 'Lato', sans-serif;
  -webkit-transition: color 0.5s, background-color 0.5s;
  transition: color 0.5s, background-color 0.5s; }

.book:hover, .book:active {
  background-color: white;
  color: black; }

.logo {
  background: transparent;
  border: white 2px dashed;
  background-color: transparent;
  position: absolute;
  top: 25%;
  width: 60%;
  left: 20%;
  text-align: center; }

.logo-text {
  font-family: 'Charmonman', cursive;
  color: white;
  font-size: 2.5rem; }

.menu-line {
  border: none;
  border-top: solid #eeecec 1vh; }

.menu-icon {
  position: absolute;
  width: 10vw;
  height: 5vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  top: 3vh;
  right: 5vw; }

.menu-icon:active {
  display: none; }

.menu-list {
  display: none; }

.menu-icon:active + .menu-list {
  list-style-type: none;
  display: flex;
  background-color: black;
  color: white;
  margin: 0;
  font-size: 2rem;
  height: 20vh;
  flex-wrap: wrap;
  justify-content: space-around;
  padding: 0;
  border: 0;
  align-items: center;
  text-align: center; }
  .menu-icon:active + .menu-list li {
    width: 30vw; }

